          <p>
            I want to take a closer look at the word cute; I use this word a lot
            and think this word is more meaningful than people think. I think this
            word can be used in different ways, some maybe more expected and some
            less, I want to look for the unexpected. And see if I can use that as
            a tool or technique in my work. Also, I wonder where the border is
            between something being cute and something that is made cute to serve
            a different purpose?
          </p>
          <p>
            The concept of cute has been ignored for a long time in serious studies
            of mass culture. Maybe it is because the word itself has been considered
            insignificant or unimportant, and, indeed, cute does not carry the weight
            of other descriptive words such as beautiful or dashing. Cute leaves a
            lot unspoken. However, when we deal with cute abstractly, it is important
            to understand the elements and concepts that are being used. Because not
            everything that looks cute is cute.
          </p>
          <p>
            Sometimes it feels like the cute is not taken seriously and is looked down
            upon, especially by the art world. Maybe because something cute can not be
            political or conceptual but is seen more as something intuitive, or just as
            an adjective. I think it can also be used as a tool to make things more
            understandable or friendlier but could also hide a dark secret, and I am
            looking forward to dive into it and find out what shapes the cute.
          </p>
        </main>
