      <!-- Chapter 2: The definition of cute -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/the-cute.svg"/>
      <br><br>
      <div class="container" id="definition">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">t</span>
            <span class="salmon">h</span>
            <span class="light-pink">e</span>
            &nbsp;
            <span class="pink">d</span>
