            <span class="red">c</span>
            <span class="light-salmon">u</span>
            <span class="salmon">t</span>
            <span class="light-pink">e</span>
          </h1>
        </header>
        <section>
          <main>
            <p>
              <i>Words change over time, and cute is no exception. The first use of cute
              dates back to 1600, it meant a <a href="#dog" aria-describedby="footnote-label" id="dog-ref">dog</a>.</i> (Since dogs are cute,
              maybe this isnt that surprising.) In the 1700s, cute was a spin-off from
              the wordacute (a sharp edge or point). Back then, cute meant clever,
              cunning,or sharp.
            </p>
            <p>
              For the last 150 years, cute has usually meant pretty and charming in an
              innocent way. Of course, there are other words for this. For instance:
              adorable, appealing, attractive, childish, comfy, comic, cuddly, delightful,
              engaging, free, friendly, frivolous, gentle, genuine, happy, innocent, inoffensive,
              lovable, lovely, natural, precious, pure, silly, simple, sincere, soft, sweet,
              sympathetic, touching and vulnerable. But cute is the adjective best of them all.
            </p>
            <p>
              <i>However, the word is also a bit undivined because it lacks specific external
              features that are needed in adjectives like beautiful or <a href="#ugly" aria-describedby="footnote-label" id="ugly-ref">ugly</a>.</i>
              Personal taste is the determining factor for things and persons being described
              as cute. Even something ugly can be cute, maybe it has cute characteristics or
              a goofy walk. The view on what is cute is personal and subjective.
            </p>
            <p>
              The list of cute things and persons is endless, but cute brings a message of
              positivity and innocence. Cute is a kind of love. <i>It relishes, rather than
              it judges, it celebrates the awkward intensities of the character Cute
              identifies with what is <a href="#enjoyingNote" aria-describedby="footnote-label" id="enjoyingNote-ref">enjoying</a>.</i>
            </p>
