        <section>
          <main>
            <p>
              Cute things dont have to be young, but it helps! A scientist named Konrad
              Lorenz looked at cuteness. He saw that almost everyone automatically thinks
              babies are cute.
            </p>
            <p>
              It doesnt stop there. Anything that even looks like a baby is also cute.
              Thats why so many baby animals are cute to us.
            </p>
            <p>
              Baby humans dont look like babies for a long time, though, and once theyre
              adults, theyre less cute (Maybe this is also why Peter Pan doesnt want to
              grow up). But there are animals that look almost the same no matter how old
              they are!
            </p>
            <p>
              In Japan there people believe for a teen to grow up means hiding behind an
              artificial layer of <a href="#adulthood" aria-describedby="footnote-label" id="adulthood-ref">adulthood</a>. Which means the precious, fun, and cute stuff
              of childhood is lost foreverunless teens hang onto it. So they sometimes do.
            </p>
            <p>
              Some adults worry this shows that Japanese teens dont want to grow up at all.
              Others think its just a unique part of their culture.
            </p>
