            </p>
            <p>
              So when Winnie the Pooh got a pot of honey stuck on his head, I felt sorry
              for him. That Silly bear. I would have helped Winnie, too, but I didnt
              want to get sticky. Plus, he was in a book. (This posed certain problems.)
            </p>
            <p>
              Human babies are so helpless, they cant even lift their heads in the beginning.
              As for being able to get that stuffed animal she dropped, forget it. Yes,
              babies need our help to survive, and we happily give it.
            </p>
