            <span class="yellow">u</span>
            <span class="red">r</span>
            <span class="light-salmon">f</span>
            <span class="salmon">u</span>
            <span class="light-pink">l</span>
            <span class="pink">l</span>
          </h1>
        </header>
        <section>
          <main>
            <p>
              I think almost any color can be cute. Thats why rainbows are so nice.
              They have all the colors of the rainbow. Choose any color in the rainbow.
              Now picture that color in its most delicate, soft version. What youre
              imagining is the pastel version of that color. Pastels can be cute, for
              instance, a soft yellow pastel seems cozy. An orange pastel might be fun
              and playful. And a light red pastel can be pink. The natural enemy of boys.
            </p>
            <p>
              But it doesnt always have to be. One of my favorite male hip hop artist
              called CamRon loves pink, he is often seen wearing pink clothing and also
              patent his own Pantone color called: Killa Pink. I like that he is showing
              his soft side by wearing and loving pink in the very male dominant business
              and of course it makes him look a bit more cute. Maybe other hip hop artists
              can take his example and wear pink, to show that pink doesnt always have to
              be a color for girls. But it does make you look a bit more soft and sweet,
              what can be helpful sometimes.
            </p>
            <p>
              Why do boys have pink-phobia? It must be because pink is a girls color.
              Except it isnt!
            </p>
            <p>
              True, pink is not thought of as a manly color. Soldiers dont have pink
              guns. Construction workers dont wear pink hard hats. And if a boy in first
              grade wears a pink shirt to school, you can bet one of his friends will say,
              Pink is for girls!
            </p>
            <p>
              <i>This wasnt actually always the case, back in 1900 the leaders in the
              clothing and greeting card businesses got together and decided that pink
              is for boys and blue is for <a href="#girls" aria-describedby="footnote-label" id="girls-ref">girls</a>.</i> Pink was considered the baby version
              of red, which was a strong, manly color. Girls were encouraged to wear baby
              blue, because it was dainty and delicate. If you look at classic Disney
              films, you can see what I mean. Blue outfits for girls are featured in
              Cinderella (1950), Alice in Wonderland (1951), Peter Pan (1953) and
              Sleeping Beauty (1959).
            </p>
            <p>
              <i>In 1960 the colour assignments slowly reversed to pink for girls and
              baby blue for <a href="#girls" aria-describedby="footnote-label" id="girls-ref">boys</a>.</i> And thats why pink is thought of a girly colour
              today. But actually it doesnt have to be.
            </p>

