            <span class="light-blue">s</span>
            &nbsp;
            <span class="blue">a</span>
            <span class="teal">r</span>
            <span class="light-green">e</span>
            &nbsp;
            <span class="green">c</span>
            <span class="neon-green">u</span>
            <span class="orange">t</span>
            <span class="light-yellow">e</span>
          </h1>
        </header>
        <section>
          <main>
            <p>
              Mascots are often used to make something look cute or to give an
              organisation more personality it can even make some organisations look
              less threating.
            </p>
            <p>
              In Japan, everything gets a cute mascot. <i>For example, Japan is broken
              up into 47 areas called prefectures. (These are like states or provinces.)
              Each prefecture has its own cute <a href="#mascot" aria-describedby="footnote-label" id="mascot-ref">mascot</a>.</i> Pretty much everything else has
              its own mascot, too. Companies, road signs, and even police stations all
              have a cute little creature that symbolises them. Together, these symbolic
              mascots are called yuru-kyara. And every year, there are competitions that
              decide who are the cutest among them.
            </p>
            <p>
              In the western culture this way of using mascots is also often used by
              different people or companies to make something look more friendly and
              personal or sell something quicker, like candy or other sweet stuff that
              is marketed to kids, that all look very cute sweet and innocent on first
              sight but the truth is, there is a lot of sugar that in a tiny piece of
              food, and its definitely not healthy and cute as it seems to be. So here
              the product is actually not cute, the company just uses cute aesthetics to
              cover up that the candy is actually not good for you by using a mascot
              and bright colours and this way the product pops into the eyes of children.
            </p>
            <p>
              Another example of using mascots to sell something is the Chinese company
              Lees who sell wan tans to the world. On all their packages they use a
              little mascot that presents the animal that is used inside the wan tans
              to make the wan tans more likeable, sellable and cute. This probably works,
              but its also sinister since the cute little mascot on the package had to
              die to create these wan tans. Maybe they should make a wan tan mascot
              instead of using the animals that are in the wan tans and put that on
              the package.
            </p>
            <p>
              Sometimes mascots are also being used in hip hop music, to show another
              side of a hip hop artist. This can be by creating an alter ego or imaginary
              friend that looks like a mascot.
            </p>
            <p>
              For example, the rapper Quasimoto created an imaginary friend called Lord
              Quas. This character is often heard and seen in his songs and video clips.
              <i>Lord Quas represents a characteristic image of the rapper <a href="#mascot" aria-describedby="footnote-label" id="mascot-ref">himself</a>.</i> You
              can recognise him by his high-pitched voice, which is a cute quality.
              For me the use of this imaginary friend shows that Quasimoto also has a
              soft side and a lot of imagination. For some people an imaginary friend
              is perhaps a bit childish. But I think in this case it can bring up
              interesting debates in songs and also help to pose questions and answers
              by using two different characters.
            </p>
            <p>
              Another example is Kanye West, who has his own mascot called dropout bear.
              Which on his debut album The College Dropout was one of those college
              mascots, that came on the cover by accident, because the mascot costume was
              lying around in the school building where they had the photo shoot for the
              cover of the album, so they used it. After that the dropout bear came back
              on three other album covers and in the video clip of Good morning where
              he is an animated character created by the Japanese artist Murakami.
            </p>
            <p>
              I was wondering if there was a reason for using this mascot because dropout
              bear seems to be a part of Kanye. Why would he otherwise put dropout bear
              on four different album covers and use it in a video clip? Perhaps he had a
              teddy bear when he was a child, it seems like he thought the school experience
              was scarybewildering to him. Teddy bears are being associated with being too
              young for College. That could also explain the doors on the Late Registration
              album cover being massive compared to the bear that resembles Kanye. Or is it
              because all the albums covers have the same theme which is college.
            </p>
            <p>
              The dropout bear has been missing from Kanyes work for a while now and I
              hope it will appear some time again. Because this mascot showed me that Kanye
              also has a soft and goofy side. What I as a 11-year-old girl really
              appreciated and maybe this was also one of the things that made me fall
              in love with Kanye.
            </p>
