      <!-- Chapter 7: Cuteness sells -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/cuteness-sells.svg"/>
      <br><br>
      <div class="container" id="cutenessSells">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">C</span>
            <span class="salmon">u</span>
            <span class="light-pink">t</span>
            <span class="pink">e</span>
            <span class="magenta">n</span>
            <span class="light-purple">e</span>
            <span class="light-blue">s</span>
            <span class="blue">s</span>
            &nbsp;
            <span class="teal">s</span>
            <span class="light-green">e</span>
            <span class="green">l</span>
            <span class="neon-green">l</span>
            <span class="orange">s</span>
          </h1>
        </header>
        <section>
        <main>
          <p>
            A cute toy sort of sells itself but are the forces of cuteness powerful
            enough to sell un-cute products? Yes, cuteness is used in ads for everything
            from car insurance to toilet paper. I mean, car insurance has to be one of
            the most boring things ever. To make their service interesting, the GEICO
            insurance company went cute by using a gecko in its ads back in 2000.
          </p>
          <p>
            First the GEICO gecko looked like a lizard and crawled on all four legs.
            Not bad, but could it be cuter? Slowly, the gecko was given bigger eyes,
            a rounder head, and a less-lizard-like look. Plus, it started standing
            upright and speaking with an accent. And that new, improved cute gecko
            has worked like a charm!
          </p>
          <p>
            While car insurance isnt cute, cars can be. Take the VW Beetle. People
            loved the old classic Beetle because it was small and round and cute.
          </p>
          <p>
            When the Beetle was updated in 1998, VW made the car even rounder. They
            added a little flower vase on the dashboard. Cute! For a small car, the
            new Beetle has big headlights. Thats because its designed so that the
            front of the car sort of looks like a smiling human face. The headlights
            are the eyes. This gives it a friendly face in the front.
          </p>
          <p>
            Here is another example of a well-known beer brewery in Holland, called
            Heineken. In 1964 Alfred Heineken wanted to re-brand his beer. He realised
            that at that at that time shopping was mainly done by women, and it was
            necessary for consumers to think positively towards Heineken over other
            brands. <i>So in 1964 Alfred changed the font on his label, he did this to
            cheer it up and the smiling es were <a href="#born" aria-describedby="footnote-label" id="born-ref">born</a>.</i> Which makes Heineken beer
            seem more positive and friendly than other beers, by just the small change
            of the rotated es. Which is actually a great selling trick, and maybe
            even why Heineken is one of the biggest beer brands in the world, who knows?
          </p>
          <p>
            Cuteness can also be add to a certain product to make it sell quicker.
            like in this add for a local supermarket Dekamarkt where they are adding
            faces to products that normally dont have a face. Or describing the
            product as a nice bucket or fun dishwashing brush. This is probably
            a strategy to make the item look cute so it will sell faster.
          </p>
          <p>
            Also, some products have been given a personality like the Henry
            vacuum cleaner from the brand Numatic. so you can see it as not only
            is a tool to clean your house but also a friend who makes the cleaning
            easier for you.
          </p>
          <p>
            Advertisers will never stop mixing and matching cute qualities to sell
            us stuff. So its up to us to understand the difference about the real
            and the fake cuteness around us. That way, we wont get tricked.
          </p>
          <footer>
            <ol>
              <li id="born">Unknown (2012), Heineken making consumers smile since 1964!!.
                <a href="#born-ref" aria-label="Back to content"></a>
              </li>
            </ol>
          </footer>
        </main>
