      <!-- Chapter 8: Cuteness can be therapeutic -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/cuteness-therapeutic.svg"/>
      <br><br>
      <div class="container" id="cutenessTherapeutic">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">C</span>
            <span class="salmon">u</span>
            <span class="light-pink">t</span>
            <span class="pink">e</span>
            <span class="magenta">n</span>
            <span class="light-purple">e</span>
            <span class="light-blue">s</span>
            <span class="blue">s</span>
            &nbsp;
            <span class="teal">c</span>
            <span class="light-green">a</span>
            <span class="green">n</span>
            &nbsp;
            <span class="neon-green">b</span>
            <span class="orange">e</span>
            &nbsp;
            <span class="light-yellow">t</span>
            <span class="yellow">h</span>
            <span class="red">e</span>
            <span class="light-salmon">r</span>
            <span class="salmon">a</span>
            <span class="light-pink">p</span>
            <span class="pink">e</span>
            <span class="magenta">u</span>
            <span class="light-purple">t</span>
            <span class="purple">i</span>
            <span class="light-blue">c</span>
          </h1>
        </header>
        <section>
        <main>
          <p>
            Research has shown that cuddling with dogs works stress relieving.
            Since dogs are cute, being around them is good fun. <i>One person describes
            just looking at pictures of puppies as Relaxing and pure happiness.
            So what happens when you get to play with a <a href="#dog-two" aria-describedby="footnote-label" id="dog-two-ref">dog</a>?</i>
          </p>
          <p>
            A bunch of people once played Operation in Dutch this game called
            Dokter Bibber. Afterward, they were sorted into two smaller groups.
            One group of players was then shown cute pictures of cats and dogs.
            The other group was shown really cute pictures of kittens and puppies.
            Then everyone went back and played Operation again. Guess what? <i>The
            people who looked at cute pictures of cats and dogs did better. They
            had good focus and their hands were <a href="#dog-two" aria-describedby="footnote-label" id="dog-two-ref">steady</a>.</i>
          </p>
          <p>
            The University of Amsterdam is offering over-stressed students the chance
            to cuddle up with puppies to relieve exam stress next week. <i>It will have a
            puppy room available with 750 seats for two afternoons in its library,
            saying that research shows that a quarter hour of puppy love can lower
            blood levels of cortisol, and therefore reduce stress during exam <a href="#exam" aria-describedby="footnote-label" id="exam-ref">week</a>.</i>
            The idea, reportedly, comes from British universities including Bristol
            and Nottingham.
          </p>
          <p>
            Its no surprise that dogs can soothe, but other benefits are less predictable.
            The animals draw out even the most isolated personality and having to praise
            the animals helps traumatised veterans overcome emotional numbness. Teaching
            the dogs service commands develops a patients ability to communicate, to be
            assertive but not aggressive, a distinction some struggle with. <i>The dogs can
            also assuage the hyper alertness common in vets with <a href="#PTSD" aria-describedby="footnote-label" id="PTSD-ref">PTSD</a>.</i> Some participants
            report they finally got some sleep knowing a naturally alert soul was standing
            watch. Dogs are such loving and helpful creatures.
          </p>
          <p>
            The television children channel Pebble TV has a special night program that
            shows only animals just being animals with the sound of a ticking clock in
            the background. Which helps for children to relax and fall a sleep. I really
            love to watch Pebble TV, for me it also works to relax and fall a sleep
            peacefully.
          </p>
          <footer>
            <ol>
              <li id="dog-two">Bart King, Cute! A guide to all things adorable, Page 158.
                <a href="#dog-two-ref" aria-label="Back to content"></a>
              </li>
              <li id="exam">S. Boztas (2018), University of Amsterdam offers puppy
                love to stressed students. <a href="#exam-ref" aria-label="Back to content"></a>
              </li>
              <li id="PTSD">C. Colin (2012), How Dogs Can Help Veterans Overcome PTSD.
                <a href="#PTSD-ref" aria-label="Back to content"></a>
              </li>
            </ol>
          </footer>
        </main>
        <aside>
          <div class="row">
            <figure>
              <img src="/go-theses-19-cato-stigter/static/images/23.png">
              <figcaption>Foto Nathan Cornetet/Fusion Photography.</figcaption>
            </figure>
          </div>
