      <br><br>
      <!-- Chapter 9: Hey, that sounds cute -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/hey-cute.svg"/>
      <br><br>
      <div class="container" id="heyCute">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">h</span>
            <span class="salmon">e</span>
            <span class="light-pink">y</span>
            <span class="pink">,</span>
            &nbsp;
            <span class="magenta">t</span>
            <span class="light-purple">h</span>
            <span class="light-blue">a</span>
            <span class="blue">t</span>
            &nbsp;
            <span class="teal">s</span>
            <span class="light-green">o</span>
            <span class="green">u</span>
            <span class="neon-green">n</span>
            <span class="orange">d</span>
            <span class="light-yellow">s</span>
            &nbsp;
            <span class="yellow">c</span>
            <span class="red">u</span>
            <span class="light-salmon">t</span>
            <span class="salmon">e</span>
            <span class="light-pink">!</span>
          </h1>
        </header>
        <section>
        <main>
          <p>
            Being around cuteness can change how we act and thinkand talk. Have
            you ever noticed that when someone is talking to a cute baby or an
            adorable kitten, they speak three octaves higher than normal?
          </p>
          <p class="indent">BABIES</p>
          <p class="indent-after">
            Infants pay more attention and respond more eagerly to baby talk than
            to normal adult conversation. <i>The playfully exaggerated and high-pitched
            tone your voice takes lights up your little ones <a href="#mind" aria-describedby="footnote-label" id="mind-ref">mind</a>.</i>
          </p>
          <p class="indent">DOGS</p>
          <p class="indent-after">
            During a series of tests, dogs were spoken to in a high-pitched voice
            and again in a normal adult voice. Researchers then monitored which
            speaker the dogs responded to and wanted to interact with.
          </p>
          <p>
            PhD student Alex Benjamin said it seemed dogs were more likely to
            favour the speaker who used a high pitch. <i>The researchers wanted to
            test whether speaking to dogs in dog-directed speech was useful to
            the animals, or whether humans did this because they view pets in the
            same way as <a href="#babies" aria-describedby="footnote-label" id="babies-ref">babies</a>.</i>
          </p>
          <p class="indent">WOMEN</p>
          <p class="indent-after">
            I noticed that some women also use this high-pitched voice when they are
            not talking to babies or dogs. For example Khloe Kardashian who is using
            this high-pitched voice when discussing certain subjects. I realised that
            when women are using this voice they are almost always talking about a
            subject that involved negative behaviour of their own that they now regret.
            <i>They either consciously or unconsciously, slip into using this immature,
            child sounding <a href="#voice" aria-describedby="footnote-label" id="voice-ref">voice</a>.</i> It seems that they use this voice to dismiss
            themselves for any blame and dont want to accept that they were wrong
            (by making themselves sound innocent and cute).
          </p>
          <p>
            Of course this only applies to women who have a normal adult female
            speaking voice. Some women naturally have high-pitched voices. If you
            pay attention to the female voices you hear in conversation, there is
            a big change that one of these women is using this child like voice.
            Its almost funny once you listen to it and hear what the subject is.
          </p>
          <p>
            Some female singers also use this voice like one of my favorite singers
            Lily Allen, who uses this very sweet high-pitched voice, which makes her
            music sound sweet and cute. But if you listen to the lyrics of her music,
            her voice can be quite deceitful. She is often saying pretty mean things,
            for instance, in the song Fuck You she sings:
          </p>
          <p class="indent"><i>VERSE 1</i></p>
          <p class="indent-after">
            <i>Look inside, look inside your tiny mind. Then look a bit harder. Cause
            were so uninspired, so sick and tired of all the hatred you harbour.
            So you say its not okay to be gay. Well, I think youre just evil.Youre
            just some racist who cant tie my laces. Your point of view is <a href="#medieval" aria-describedby="footnote-label" id="medieval-ref">medieval</a></i>
          </p>
          <p>
            Lily Allen has said this song is about (then) U.S. President George W.
            Bush. Many people speculated that G.W. was trying to make a legacy like
            his dads and make him proud.
          </p>
          <p>
            Lily here says being small-minded and alienating people, like the LGBT
            community (and if Kanye is to be believed, the black community) is not
            going to work out for the better.
          </p>
          <p>
            Another example from Lily is The Fear where she sings out her point
            of view on women in the entertainment industry. Maybe its a bit sarcastic
            but there is a certianly truth in it.
          </p>
          <p class="indent"><i>VERSE 1</i></p>
          <p class="indent-after">
            <i>I wanna be rich and I want lots of money. I dont care about clever,
            I dont care about funny. I want loads of clothes and fuckloads of
            diamonds. I heard people die while theyre trying to find them and Ill
            take my clothes off and it will be shameless. Cause everyone knows
            thats how you get famous. Ill look at the sun and Ill look in the mirror,
            Im on the right track, yeah, Im on to a <a href="#winner" aria-describedby="footnote-label" id="winner-ref">winner</a></i>
          </p>
          <p>
            Some women attempt to be famous doing nothing worthwhile, like reality
            TV stars, glamour models, and the wives and children of legitimate celebrities.
            Their goal is to be famous and rich rather than achievement or success
            or developing their own character. Whats the point of being rich if
            you can not be yourself?
          </p>
          <p>
            So maybe the reason Lily is using this voice is that she wants to make
            sure that her message is heard by everyone who listens to her music.
            Maybe by sounding cute there is a bigger change that people are listening,
            in the same way that babies and dogs pay more attention to a high-pitched
            voice.
          </p>
          <footer>
            <ol>
              <li id="mind">Bart King, Cute! A guide to all things adorable, Page 103.
                <a href="#mind-ref" aria-label="Back to content"></a>
              </li>
              <li id="babies">2. Unkown (2018), Why using baby-talk improves our bond with dogs.
                <a href="#babies-ref" aria-label="Back to content"></a>
              </li>
              <li id="voice">I. L. J. Mitchell (2016), Why do some adult women talk with a childish voice?
                <a href="#voice-ref" aria-label="Back to content"></a>
              </li>
              <li id="medieval">Fuck You, Lily Allen, Album: Its Not Me, Its You.
                <a href="#medieval-ref" aria-label="Back to content"></a>
              </li>
              <li id="winner">The Fear, Lily Allen, Album: Its Not Me, Its You.
                <a href="#winner-ref" aria-label="Back to content"></a>
              </li>
            </ol>
          </footer>
        </main>
      </section>
