      <!-- Chapter 10: Cuteness in relation to drag queens -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/cuteness-queens.svg"/>
      <br><br>
      <div class="container" id="cutenessDrag">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">C</span>
            <span class="salmon">u</span>
            <span class="light-pink">t</span>
            <span class="pink">e</span>
            <span class="magenta">n</span>
            <span class="light-purple">e</span>
            <span class="light-blue">s</span>
            <span class="blue">s</span>
            &nbsp;
            <span class="teal">i</span>
            <span class="light-green">n</span>
            &nbsp;
            <span class="green">r</span>
            <span class="neon-green">e</span>
            <span class="orange">l</span>
            <span class="light-yellow">a</span>
            <span class="yellow">t</span>
            <span class="red">i</span>
            <span class="light-salmon">o</span>
            <span class="salmon">n</span>
            &nbsp;
            <span class="light-pink">t</span>
            <span class="pink">o</span>
            &nbsp;
            <span class="magenta">d</span>
            <span class="light-purple">r</span>
            <span class="purple">a</span>
            <span class="light-blue">g</span>
            <span class="blue">q</span>
            <span class="teal">u</span>
            <span class="light-green">e</span>
            <span class="green">e</span>
            <span class="neon-green">n</span>
            <span class="light-salmon">s</span>
          </h1>
        </header>
        <section>
          <main>
            <p>
              <i>The peculiar relation between cute and drag queens has to be explained.
              While its not true that all drag queens are, there is no doubt a peculiar
              affinity and <a href="#overlap" aria-describedby="footnote-label" id="overlap-ref">overlap</a>. The cute insistence on not being serious,
              on playing, also connects with the drag queens desire to remain <a href="#overlap" aria-describedby="footnote-label" id="overlap-ref">youthful</a>.</i>
            </p>
            <p>
              To me a lot of drag queens are surely cute, like Kim Chi and Trixie Mattel.
              But my sister thinks drag queens are meer kattig dan schattig. Which I
              can agree on, but I think its because they are more a caricature version
              of cute. There can also be parts of a queen that are cute like the fact
              they love to wear bright colours and loads of glitter. Or maybe its because
              drag queens are always over the top, more that a real woman. They have
              too much of everything. It feels as if playing with dolls when I was little
              and making up stories of how I would be when I grow up (Which never was too
              realistic). But then the drag queens became a caricature of those dolls for
              me. They are wearing the outfits I used to put on my Barbie dolls. Sometimes
              drag queens also remind me of Disney princesses that are perfectly polished
              and wear bright colourful glitter gowns.
            </p>
            <p class="indent">CUTE ELEMENTS</p>
            <p class="indent-after">
              Cuteness can be a small element of a bigger thing, it can be a colour, a
              shape, maybe someones voice or way of he or she is walking or an expression.
              It can be a feeling or just something that is fuzzy. Something also can be
              seen as cute because its tiny, for instance, a tiny ketchup bottle is cute
              but a normal-sized ketchup bottle is not.
            </p>
            <p class="indent">KIM CHI</p>
            <p class="indent-after">
              One of my favourite and therefore one of the cutest drag queens is Kim
              Chi. She is very goofy and clumsy in her way of moving and talking which
              sometimes is a little childlike. Also, her looks are always very imaginative,
              she wears a lot of bright and pastel colours. She always has big eyes, and
              big hair, just like an anime character. She definitely has some of that
              kawaii style in or out of drag and I am not the only one who thinks
              that, a lot of Reddit readers agree with me as they respond to a
              Snapchat picture of Kim Chi out of drag. Also, the fact that a lot of
              her outfits are inspired by by cartoon characters like Tweety or Hello
              Kitty is cute.
            </p>
            <p class="indent">TRIXIE MATTEL</p>
            <p class="indent-after">
              Another drag queen that is surely cute is Trixie Mattel. She embodies
              the character of a Barbie doll in a caricature way. Which is probably
              why her last name is Mattel, like the brand that created Barbie. More
              resemblance between Trixie and Barbie are that their favourite colour
              is pink, and that they both have blond hair. Trixie her makeup is doll-like
              (not the same as Barbie, but more in a human doll sort of way). The
              queen said she found inspiration for her makeup in toys she always wanted
              to play with as a kid, but that society frowned upon. Her makeup is not
              very traditional for a drag queen, she also got some comments on her
              makeup by other drag queens. Like Acid Betty, who said: <i>Look at that
              ugly makeup, lets be honest, That nose contour is <a href="#atrocious" aria-describedby="footnote-label" id="atrocious-ref">atrocious.</a></i> But
              Trixie is actually not at all bothered by these comments.
            </p>
            <p>
              Overall maybe not all drag queens are cute, but definitely a lot of
              elements from drag queens are cute. And maybe the look of drag queens
              is also changing, the drag queens now are more polished and playing the
              role of a cartoon caricature more than they were before.
            </p>
            <footer>
              <ol>
                <li id="overlap">Susan Sontag, Notes on camp, Page 29.
                  <a href="#overlap-ref" aria-label="Back to content"></a>
                </li>
                <li id="babies">Susan Sontag, Notes on camp, Page 33.
                  <a href="#babies-ref" aria-label="Back to content"></a>
                </li>
                <li id="atrocious">A. Salandra (2018), Drag Race: Why Does Trixie Mattel Look
                  so Different from Other Queens?.
                  <a href="#atrocious-ref" aria-label="Back to content"></a>
                </li>
              </ol>
            </footer>
          </main>
