      <br><br>
      <!-- Chapter 11: Cuteness and propaganda -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/cuteness-propaganda.svg"/>
      <br><br>
      <div class="container" id="cutenessPropaganda">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">C</span>
            <span class="salmon">u</span>
            <span class="light-pink">t</span>
            <span class="pink">e</span>
            <span class="magenta">n</span>
            <span class="light-purple">e</span>
            <span class="light-blue">s</span>
            <span class="blue">s</span>
            &nbsp;
            <span class="teal">a</span>
            <span class="light-green">n</span>
            <span class="green">d</span>
            &nbsp;
            <span class="neon-green">p</span>
            <span class="orange">r</span>
            <span class="light-yellow">o</span>
            <span class="yellow">p</span>
            <span class="red">a</span>
            <span class="light-salmon">g</span>
            <span class="salmon">a</span>
            <span class="light-pink">n</span>
            <span class="pink">d</span>
            <span class="magenta">a</span>
          </h1>
        </header>
        <section>
        <main>
          <p>
            When a politician runs for election, he or she loves to be shown holding
            babies. The idea is that anyone who likes babies has to be loving and
            caring (Yes; I know this is silly.) <i>This technique is used by many
            presidents and president candidates to win more <a href="#votes" aria-describedby="footnote-label" id="votes-ref">votes</a>.</i> But maybe it
            is also because children are the targets of adult propaganda and a little
            you on a picture with one of those big guys has to be cherished, and
            looked back upon many times. So maybe it can already change or put a
            stamp on your political choice for later without knowing it.
          </p>
          <p>
            This technique is used by dictators like Kim Jong Un, but I dont think
            it works in this image, most people in the photograph look horrified or
            very sad. Except for Kim Jong Un and the baby he is holding. If you cut
            all those said looking people out of the image and only show Kim Jong Un
            and the happy baby, he is holding. The image suddenly becomes cute and
            caring, like the character of the dictator as he wants us to believe.
          </p>
          <p>
            Here is another example from President Putin, who tries to create an
            outdoor, sporty, tough guy public image, showing his physical capabilities
            and taking part in unusual or dangerous acts, such as extreme sports
            and interaction with wild animals. For example, in 2007, the tabloid
            Komsomolskaya Pravda published a huge photograph of a bare-chested
            Putin vacationing in the Siberian mountains under the headline: Be
            Like Putin. Such photo ops are part of a public relations approach
            that, according to Wired, <i>deliberately cultivates the macho, take-charge
            superhero <a href="#image" aria-describedby="footnote-label" id="image-ref">image</a>.</i>
          </p>
          <p>
            But on the opposite side of that he is also trying to create a loving
            and caring image as you can see on this picture where he is holding a
            newborn baby in a hospital in Russia. Which is approved and looked up
            on by the men who are standing behind him as they are smiling friendly
            to Putin and the baby and forgetting all the problems in Russia because
            Putin is a loving and caring man (or at least he seems to be in this
            picture).
          </p>
          <p>
            This is a picture taken of Donald Trump during the campaign rally in
            November 2016. On this picture he is holding a 6-month-old girl, wearing
            an American flag print. She is probably just as much of a patriot as
            Donald himself. That is the feeling I get when I see this picture.
            Holding the baby makes him look soft and sweet and probably also made
            people vote for Trump and believing he really is this soft and sweet
            guy, which we know he is not.
          </p>
          <p>
            There are also many people who use this technique on social media and
            dating apps. The have profile pictures with cute babies from families
            and friends (but not theirs). To show that they are loving and caring
            people. These photos always confuse me, I literally think the child is
            theirs and dont really understand why you want people to think you have
            a baby while you dont. Also, I think many people on dating apps would
            be less interested to date if they see a baby on the picture, but maybe
            other people see it differently.
          </p>
          <footer>
            <ol>
              <li id="votes">Bart King, Cute! A guide to all things adorable, Page 162.
                <a href="#votes-ref" aria-label="Back to content"></a>
              </li>
              <li id="image">Susan Sontag, Notes on camp, Page 33.
                <a href="#image-ref" aria-label="Back to content"></a>
              </li>
            </ol>
          </footer>
