      <!-- Chapter 12: Cuteness is innocent -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/cuteness-innocent.svg"/>
      <br><br>
      <div class="container" id="cutenessInnocent">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">C</span>
            <span class="salmon">u</span>
            <span class="light-pink">t</span>
            <span class="pink">e</span>
            <span class="magenta">n</span>
            <span class="light-purple">e</span>
            <span class="light-blue">s</span>
            <span class="blue">s</span>
            &nbsp;
            <span class="teal">i</span>
            <span class="light-green">s</span>
            &nbsp;
            <span class="green">i</span>
            <span class="neon-green">n</span>
            <span class="orange">n</span>
            <span class="light-yellow">o</span>
            <span class="yellow">c</span>
            <span class="red">e</span>
            <span class="light-salmon">n</span>
            <span class="salmon">t</span>
          </h1>
        </header>
        <section>
        <main>
          <p>
            Because cute things are innocent, we have kind feelings for them. That
            means: More Cuteness means more kindness and more friendliness when we
            are around cute things.
          </p>
          <p>
            This cute innocent look can also be a mask, for example, the Japanese
            artist Yoshitomo Nara paints a lot of young innocent looking girls with
            big heads. Although the children and dogs he creates are adorable they
            are often ominous, causing viewers to contemplate the feelings and concepts
            behind his work. Underneath the popular appeal of the dark but adorable
            characters in his work are the somber social, political, and personal
            elements of his work: darker emotions of loneliness in a rigid society,
            rage, fear, and helplessness. Nara uses soft pastels and the apiece of
            cute and vulnerable children and animals as opposed to punk rock imagery.
            He also takes inspiration from the positive values of Japanese tradition
            and combines traditional with contemporary. The subjects of his work,
            the wide-eyed children and animals, whose vulnerability and the nightmarish
            features of his paintings can easily stimulate distressing feelings. He
            often uses soft pastel colours with bold lines as seen in anime characters
            in popular culture. The children featured in Naras works sometimes wield
            weapons such as knives and saws; their expressions are haunting, their
            eyes giving viewers accusatory looks. It is with his use of contrasting
            images, colours, and emotions that Naras work has captured the
            imaginations of generations around the world.
          </p>
          <p>
            Most young girls (including me when I was young.) want to become famous
            singers or actors. When your young this might seem innocent, but there
            are always consequences that tend to be forgotten. Why should you think
            about the consequences? This is just a dream, right?
          </p>
          <p>
            For some girls this really happens. For instance take Britney Spears,
            she was one of my favorite musicians when I was younger. Britney became
            famous at the age of eleven, she got a role in the television show The
            Mickey Mouse Club and became famous. When the Mickey Mouse Club was
            canceled four years later, she decided that she wanted to become a pop
            star. At the age of seventeen she had her first hit:  Baby One More
            Time. In the video clip she wears a costumized school uniform and pink
            scrunchies which looked innocent and sweet at that time. I could not
            see that actually <i>it touches upon taboo, of a young girl in a provocative
            school girl <a href="#outfit" aria-describedby="footnote-label" id="outfit-ref">outfit.</a></i> And saw Britney as an example for what I wanted to
            be. Later on a saw all the bad sides of being famous while being young
            and I felt a sorry for her. While the paparazzi followed her every move
            and slowly pushed her to a breaking point. I think the dream of being
            famous is being glorified by the media around us, only showing the
            glamorous side of it and showing the consequences.
          </p>
          <p>
            In Japan pop culture there is a big craze of young girls being idols.
            Which are girls that sing, dance, look cute and innocent. These <i>idols
            are intended to be role models. They are supposed to maintain a good
            public image and be good examples for young <a href="#people" aria-describedby="footnote-label" id="people-ref">people.</a></i> While their target
            audience are supposed to be young the opposite is happening, in the
            documentary Tokyo Idols I saw that the fans of these idols were mostly
            middle-aged men, worshipping and idolizing these girls. The men in this
            documentary are following these girls everywhere spending a lot of money
            on cds, merchandise, shows, handshake events and meet-and-greets. The
            men who do this are super fans and they are called Otaku, they follow
            their idols everywhere. Which this is quite creepy in my eyes it seems
            the attention is accepted and liked by the idols. One of the idols called
            Rio encourages her fans to be Otaku, as she sings in one of her songs
            Some ask, should I stop being an Otaku?, But being Otaku is for life.
          </p>
          <p>
            Most of the Otakus see more in these girls that just pop stars they see
            them as a little girl fantasy. Japanese men have always prized purity
            in women. The idol culture feeds on that. Worshipping virginity. The
            men are adoring that these girls are still virgins and pure and dont
            want them to grow up and become strong women. Also, these handshake
            events might look innocent they are actually the opposite. <i>Historically
            in Japan, a handshake was a very sexual gesture. Touching was forbidden.
            Its only in the last few decades that handshakes have become accepted.
            From the artists perspective, it seems very innocent. But for the fans,
            theres probably a sexual <a href="#component" aria-describedby="footnote-label" id="component-ref">component.</a></i>
          </p>
          <p>
            I am a bit creeped out by this idol culture, since young girls see the
            idols as their example and the media presents <i>the idol fantasy, covered
            in glitter. Its sold as a platform where dreams come <a href="#true" aria-describedby="footnote-label" id="true-ref">true.</a></i> While
            actually a big part of your childhood is lost because you are working
            all the time there is not time for studying or hanging out with your
            friends or boyfriend. The condition for being part of such a successful
            act is that the girls must not date boys, so as not to shatter their
            fans illusions and stay pure and cute.
          </p>
          <p>
            A member of the biggest idol group in Japan the AKB48 was caught
            leaving her boyfriends house. After AKB48s management office demoted
            her to a trainee team as a punishment for causing a nuisance to the
            fans. To apologize she posted a video on social media where she apologized
            and shaved her head, this is a traditional form of showing contrition in
            Japan. I feel so bad for this girl, shaming herself for something that
            is normal when you are a teenager. I think this idol culture creates a
            distorted image of growing up.
          </p>
          <p>
            To me this idol culture feels a bit like a cult where these idol girls
            are the cult leaders (without knowing it of course). They are targeting
            middle age vulnerable men who have a very low sense of self worth to
            become their followers and taking them to a place where they dont have
            to doubt themselves anymore where they have some sort solidarity.
            Slowly losing track of their everyday normal life, the men are tied
            down to their idols worshipping them and putting all their time and
            effort into visiting them and spending all their of money on supporting
            them.
          </p>
          <p>
            Of course these girls dont see what is actually happening, they just
            want to be famous. While being managed by big forces that are <i>protecting
            male fantasies and provide comfort for <a href="#men" aria-describedby="footnote-label" id="men-ref">men.</a></i> I think its time for
            Japan to be introduced to other idols, perhaps maybe drag queens could
            be the answer?
          </p>
          <p>
            Drag queens are a medium full of acceptance, it doesnt matter who you
            are or where you come from, you can truly be yourself. Sure it has a
            big sarcastic voice to it but underneath that is a lot of love and
            acceptance and humour can also be very lovable thing wright?
          </p>
