      <!-- Chapter 13: The downside of being a cartoon animal -->
      <TitleImage src="/go-theses-19-cato-stigter/static/images/titles/the-animal.svg"/>
      <br><br>
      <div class="container" id="downsideCartoon">
        <header>
          <h1 class="fulljustify">
            <span class="light-salmon">t</span>
            <span class="salmon">h</span>
            <span class="light-pink">e</span>
            &nbsp;
            <span class="pink">d</span>
            <span class="magenta">o</span>
            <span class="light-purple">w</span>
            <span class="light-blue">n</span>
            <span class="blue">s</span>
            <span class="teal">i</span>
            <span class="light-green">d</span>
            <span class="green">e</span>
            &nbsp;
            <span class="neon-green">o</span>
            <span class="orange">f</span>
            &nbsp;
            <span class="light-yellow">b</span>
            <span class="yellow">e</span>
            <span class="red">i</span>
            <span class="light-salmon">n</span>
            <span class="salmon">g</span>
            &nbsp;
            <span class="light-pink">a</span>
            &nbsp;
            <span class="pink">c</span>
            <span class="magenta">a</span>
            <span class="light-purple">r</span>
            <span class="purple">t</span>
            <span class="light-blue">o</span>
            <span class="blue">o</span>
            <span class="teal">n</span>
            &nbsp;
            <span class="light-green">a</span>
            <span class="green">n</span>
            <span class="neon-green">i</span>
            <span class="light-salmon">m</span>
            <span class="salmon">a</span>
            <span class="light-pink">l</span>
          </h1>
        </header>
        <section>
        <main>
          <p>
            We are blinded by the cute, so we cant see the downside of being a
            cartoon animal. But there actually is a downside. <i>Cute cartoon characters
            like Hello Kitty, Totoro and Nijntje have stubbly arms, no fingers, no
            mouths, huge heads, massive eyes  which can hide no private thoughts
            from the viewer  nothing between their legs, pot bellies, swollen legs
            and pigeon feet, if they have feet at all. Cute things cant walk, cant
            talk, cant in fact do anything at all for themselves because they are
            physically <a href="#handicapped" aria-describedby="footnote-label" id="handicapped-ref">handicapped.</a></i>
          </p>
          <p>
            Even do these cartoon animals are physically handicapped they are still
            able to do things that are actually not possible with these handicaps,
            which makes us believe that there is no harm in it.
          </p>
          <p>
            A couple of guys named Tim wrote a poem about the fact that Hello Kitty
            has no mouth. Questioning with a lot of irony why Hello Kitty can do
            all these things if she has no mouth:
          </p>
          <p class="quote">
          <i>Hello Kitty has no mouth, and yet she is sooooo hungry.
            Hello Kitty has no mouth, and yet her dentist always gives her a sugar-free lollipop.
            Hello Kitty has no mouth, and yet children are starving in <a href="#handicapped" aria-describedby="footnote-label" id="handicapped-ref">Africa.</a></i>
          </p>
          <p>
            To me this poem is ironic, its the grown up view on a child-like
            character. The realisation of when you grow up that some things can
            not be wright like the fact that <i>Hello Kitty has no <a href="#mouth" aria-describedby="footnote-label" id="mouth-ref">mouth</a>.</i> Or the
            fact that Saint Nicholas brings presents to the whole country in one
            night, which is simply not possible.
          </p>
          <footer>
            <ol>
              <li id="handicapped">1. Maja Brzozowska-Brywczyska, Monstrous/Cute. Notes on the ambivalent
                nature of Cuteness.
                <a href="#handicapped-ref" aria-label="Back to content"></a>
              </li>
              <li id="mouth">Various Tims (19982001), Hello Kitty Has No Mouth.
                <a href="#mouth-ref" aria-label="Back to content"></a>
              </li>
            </ol>
          </footer>
        </main>
        <aside>
          <div class="row">
            <figure>
              <img src="/go-theses-19-cato-stigter/static/images/46.png">
