rs dont wear pink hard hats. And if a boy in first\n            grade wears a pink shirt to school, you can bet one of his friends will say,\n            Pink is for girls!\n          ")]),_c('p',[_c('i',[_vm._v("This wasnt actually always the case, back in 1900 the leaders in the\n            clothing and greeting card businesses got together and decided that pink\n            is for boys and blue is for "),_c('a',{attrs:{"href":"#girls","aria-describedby":"footnote-label","id":"girls-ref"}},[_vm._v("girls")]),_vm._v(".")]),_vm._v(" Pink was considered the baby version\n            of red, which was a strong, manly color. Girls were encouraged to wear baby\n            blue, because it was dainty and delicate. If you look at classic Disney\n            films, you can see what I mean. Blue outfits for girls are featured in\n            Cinderella (1950), Alice in Wonderland (1951), Peter Pan (1953) and\n            Sleeping Beauty (1959).\n          ")]),_c('p',[_c('i',[_vm._v("In 1960 the colour assignments slowly reversed to pink for girls and\n            baby blue for "),_c('a',{attrs:{"href":"#girls","aria-describedby":"footnote-label","id":"girls-ref"}},[_vm._v("boys")]),_vm._v(".")]),_vm._v(" And thats why pink is thought of a girly colour\n            today. But actually it doesnt have to be.\n          ")]),_c('footer',[_c('ol',[_c('li',{attrs:{"id":"girls"}},[_vm._v("Bart King, Cute! A guide to all things adorable, Page 113.\n                "),_c('a',{attrs:{"href":"#girls-ref","aria-label":"Back to content"}},[_vm._v("")])])])])]),_c('aside',[_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/5.jpg"}}),_c('figcaption',[_vm._v("Killa Pink.")])])]),_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/6.png"}}),_c('figcaption',[_vm._v("CamRon in his famous pink outfit during New York fashion week, 2002.")])])]),_c('div',{staticClass:"row"},[_c('fi