ing massive compared to the bear that resembles Kanye. Or is it\n            because all the albums covers have the same theme which is college.\n          ")]),_c('p',[_vm._v("\n            The dropout bear has been missing from Kanyes work for a while now and I\n            hope it will appear some time again. Because this mascot showed me that Kanye\n            also has a soft and goofy side. What I as a 11-year-old girl really\n            appreciated and maybe this was also one of the things that made me fall\n            in love with Kanye.\n          ")]),_c('footer',[_c('ol',[_c('li',{attrs:{"id":"mascot"}},[_vm._v("Bart King, Cute! A guide to all things adorable, Page 176.\n                "),_c('a',{attrs:{"href":"#mascot-ref","aria-label":"Back to content"}},[_vm._v("")])]),_c('li',{attrs:{"id":"himself"}},[_vm._v("P. Macia (2005), The Unseen.\n                "),_c('a',{attrs:{"href":"#himself-ref","aria-label":"Back to content"}},[_vm._v("")])])])])]),_c('aside',[_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/8.jpg"}}),_c('figcaption',[_vm._v("Yuru-kara competition in Japan.")])])]),_c('div',{staticClass:"row bottom"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/9.png"}}),_c('figcaption',[_vm._v("A healthier way of advertising.")])]),_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/10.jpg"}}),_c('figcaption',[_vm._v("Haribo Mascot.")])])]),_c('div',{staticClass:"row bottom"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/11.jpg"}}),_c('figcaption',[_vm._v("Lees pork wan tan.")])])]),_c('div',{staticClass:"row bottom"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/12.jpg"}}),_c('figcaption',[_vm._v("Lord Quas.")])])]),_c('div',{staticClass:"row bottom"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/13.png"}})]),_c('figure',[_c('img',{a