uteness can be a small element of a bigger thing, it can be a colour, a\n            shape, maybe someones voice or way of he or she is walking or an expression.\n            It can be a feeling or just something that is fuzzy. Something also can be\n            seen as cute because its tiny, for instance, a tiny ketchup bottle is cute\n            but a normal-sized ketchup bottle is not.\n          ")]),_c('p',{staticClass:"indent"},[_vm._v("KIM CHI")]),_c('p',{staticClass:"indent-after"},[_vm._v("\n            One of my favourite and therefore one of the cutest drag queens is Kim\n            Chi. She is very goofy and clumsy in her way of moving and talking which\n            sometimes is a little childlike. Also, her looks are always very imaginative,\n            she wears a lot of bright and pastel colours. She always has big eyes, and\n            big hair, just like an anime character. She definitely has some of that\n            kawaii style in or out of drag and I am not the only one who thinks\n            that, a lot of Reddit readers agree with me as they respond to a\n            Snapchat picture of Kim Chi out of drag. Also, the fact that a lot of\n            her outfits are inspired by by cartoon characters like Tweety or Hello\n            Kitty is cute.\n          ")]),_c('p',{staticClass:"indent"},[_vm._v("TRIXIE MATTEL")]),_c('p',{staticClass:"indent-after"},[_vm._v("\n            Another drag queen that is surely cute is Trixie Mattel. She embodies\n            the character of a Barbie doll in a caricature way. Which is probably\n            why her last name is Mattel, like the brand that created Barbie. More\n            resemblance between Trixie and Barbie are that their favourite colour\n            is pink, and that they both have blond hair. Trixie her makeup is doll-like\n            (not the same as Barbie, but more in a human doll sort of way). The\n            queen said she found inspiration for her makeup in toys she always wanted\n            to play with as a kid, but that society frowned upon. Her makeup is not\n            very traditional for a drag queen, she also got some comments on her\n            makeup by other drag queens. Like Acid Betty, who said: "),_c('i',[_vm._v("Look at that\n            ugly makeup, lets be honest, That nose contour is "),_c('a',{attrs:{"href":"#atrocious","aria-describedby":"footnote-label","id":"atrocious-ref"}},[_vm._v("atrocious.")]),_vm._v("")]),_vm._v(" But\n            Trixie is actually not at all bothered by these comments.\n          ")]),_c('p',[_vm._v("\n            Overall maybe not all drag queens are cute, but definitely a lot of\n            elements from drag queens are cute. And maybe the look of drag queens\n            is also changing, the drag queens now are more polished and playing the\n            role of a cartoon caricature more than they were before.\n          ")]),_c('footer',[_c('ol',[_c('li',{attrs:{"id":"overlap"}},[_vm._v("Susan Sontag, Notes on camp, Page 29.\n                "),_c('a',{attrs:{"href":"#overlap-ref","aria-label":"Back to content"}},[_vm._v("")])]),_c('li',{attrs:{"id":"babies"}},[_vm._v("Susan Sontag, Notes on camp, Page 33.\n                "),_c('a',{attrs:{"href":"#babies-ref","aria-label":"Back to content"}},[_vm._v("")])]),_c('li',{attrs:{"id":"atrocious"}},[_vm._v("A. Salandra (2018), Drag Race: Why Does Trixie Mattel Look\n                so Different from Other Queens?.\n                "),_c('a',{attrs:{"href":"#atrocious-ref","aria-label":"Back to content"}},[_vm._v("")])])])])]),_c('aside',[_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/26.png"}}),_c('figcaption',[_vm._v("Rupauls Drag Race season 10, finale family picture.")])])]),_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/27.png"}}),_c('figcaption',[_vm._v("Disney princesses.")])])]),_c('div',{staticClass:"row marg-lef