[_vm._v("a")]),_c('span',{staticClass:"green"},[_vm._v("n")]),_c('span',{staticClass:"neon-green"},[_vm._v("i")]),_c('span',{staticClass:"light-salmon"},[_vm._v("m")]),_c('span',{staticClass:"salmon"},[_vm._v("a")]),_c('span',{staticClass:"light-pink"},[_vm._v("l")])])]),_c('section',[_c('main',[_c('p',[_vm._v("\n          We are blinded by the cute, so we cant see the downside of being a\n          cartoon animal. But there actually is a downside. "),_c('i',[_vm._v("Cute cartoon characters\n          like Hello Kitty, Totoro and Nijntje have stubbly arms, no fingers, no\n          mouths, huge heads, massive eyes  which can hide no private thoughts\n          from the viewer  nothing between their legs, pot bellies, swollen legs\n          and pigeon feet, if they have feet at all. Cute things cant walk, cant\n          talk, cant in fact do anything at all for themselves because they are\n          physically "),_c('a',{attrs:{"href":"#handicapped","aria-describedby":"footnote-label","id":"handicapped-ref"}},[_vm._v("handicapped.")]),_vm._v("")])]),_c('p',[_vm._v("\n          Even do these cartoon animals are physically handicapped they are still\n          able to do things that are actually not possible with these handicaps,\n          which makes us believe that there is no harm in it.\n        ")]),_c('p',[_vm._v("\n          A couple of guys named Tim wrote a poem about the fact that Hello Kitty\n          has no mouth. Questioning with a lot of irony why Hello Kitty can do\n          all these things if she has no mouth:\n        ")]),_c('p',{staticClass:"quote"},[_c('i',[_vm._v("Hello Kitty has no mouth, and yet she is sooooo hungry.\n          Hello Kitty has no mouth, and yet her dentist always gives her a sugar-free lollipop.\n          Hello Kitty has no mouth, and yet children are starving in "),_c('a',{attrs:{"href":"#handicapped","aria-describedby":"footnote-label","id":"handicapped-ref"}},[_vm._v("Africa.")]),_vm._v("")])]),_c('p',[_vm._v("\n          To me this poem is ironic, its the grown up view on a child-like\n          character. The realisation of when you grow up that some things can\n          not be wright like the fact that "),_c('i',[_vm._v("Hello Kitty has no "),_c('a',{attrs:{"href":"#mouth","aria-describedby":"footnote-label","id":"mouth-ref"}},[_vm._v("mouth")]),_vm._v(".")]),_vm._v(" Or the\n          fact that Saint Nicholas brings presents to the whole country in one\n          night, which is simply not possible.\n        ")]),_c('footer',[_c('ol',[_c('li',{attrs:{"id":"handicapped"}},[_vm._v("1. Maja Brzozowska-Brywczyska, Monstrous/Cute. Notes on the ambivalent\n              nature of Cuteness.\n              "),_c('a',{attrs:{"href":"#handicapped-ref","aria-label":"Back to content"}},[_vm._v("")])]),_c('li',{attrs:{"id":"mouth"}},[_vm._v("Various Tims (19982001), Hello Kitty Has No Mouth.\n              "),_c('a',{attrs:{"href":"#mouth-ref","aria-label":"Back to content"}},[_vm._v("")])])])])]),_c('aside',[_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/46.png"}}),_c('figcaption',[_vm._v("Hello Kitty.")])])]),_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/47.png"}}),_c('figcaption',[_vm._v("Image from My neighbor Totoro, by Hayao Miyazaki, 1988.")])])]),_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/48.jpg"}}),_c('figcaption',[_vm._v("Nijntje.")])])])])])]),_c('br'),_c('br'),_c('TitleImage',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/titles/opposites-attract.svg"}}),_c('br'),_c('br'),_c('div',{staticClass:"container",attrs:{"id":"oppositesAttract"}},[_c('header',[_c('h1',{staticClass:"fulljustify"},[_c('span',{staticClass:"light-salmon"},[_vm._v("o")]),_c('span',{staticClass:"salmon"},[_vm._v("p")]),_c('span',{staticClass:"light-pink"},[_vm._v("p")]),_c('span',{staticClass:"pink"},[_vm._v("o")]),_c('span',{staticClass:"magenta"},[_vm._v("s")]),_c('span',{staticClass:"light-purple"},[_vm._v("i")]),_c('span',{staticClass:"light-blue"},[_vm._v("t")]),_c('span',{staticClass:"blue"},[_vm._v("e")]),_c('span',{staticClass:"teal"},[_vm._v("s")]),_vm._v("\n          \n          "),_c('span',{staticClass:"light-green"},[_vm._v("a")]),_c('span',{staticClass:"green"},[_vm._v("t")]),_c('span',{staticClass:"neon-green"},[_vm._v("t")]),_c('span',{staticClass:"orange"},[_vm._v("r")]),_c('span',{staticClass:"light-yellow"},[_vm._v("a")]),_c('span',{staticClass:"yellow"},[_vm._v("c")]),_c('span',{staticClass:"red"},[_vm._v("t")])])]),_c('section',[_c('main',[_c('p',[_vm._v("\n          Cuteness has a power that can make the opposite of cute, cute. This can\n          happen by adding certain cute elements to it or adding the word cute to\n          it sometimes like this one:\n        ")]),_c('p',[_vm._v("\n          I found this cute gang on the toilet of the OT301 in Amsterdam and\n          was very intrigued by it. So I made a picture. Since these two words\n          are the opposites of each other. But the use of cute in the word also\n          makes gang sound cute, as I imagine the cute gang to be sweet, bright\n          and colourful girls instead of an organised group of criminals. And I\n          would suddenly love to be part of this gang.\n        ")]),_c('p',[_vm._v("\n          The same happens with this image parody of Hello Kitty with a Kalashnikov.\n          The Kalashnikov itself is the opposite of sweet. But when Hello Kitty\n          is holding it, it becomes a grey area of cute and has the possibility\n          to turn into a sweet and innocent object. I dont believe Hello Kitty\n          would use the gun, but maybe who knows?\n        ")]),_c('p',[_vm._v("\n          Maybe these girls are the cute gang, girls wearing Hello Kitty shirts\n          and holding guns. They definitely look cuter than if it would be men\n          in military outfits holding guns, I find it really interesting that\n          parents let their daughters hold guns let them pose with the guns. It\n          sends a strange message and also glorifies the gun as an object to\n          pose with, (the same as what Hello Kitty does on the shirt.) while not\n          standing still what this object can do.\n        ")]),_c('p',[_vm._v("\n          While I was scrolling thru the internet I found the nice image from\n          he artist Beni Bischof that shows a volcano explosion, which is probably\n          some kind op Photoshop collage. It really intrigued me, it felt as if\n          the Mickey Mouse shape and face made the explosion look friendly and\n          hide the fact that an explosion is actually very dangerous and can be\n          deadly. Which reminds me that a lot of Hurricanes are named after\n          females, such as Hurricane Katrina or Hurricane Sandy. According\n          to a new study people subconsciously assume that storms with feminine\n          names will be less dangerous, which makes the hurricane actually more\n          deathly.\n        ")]),_c('p',[_vm._v("\n          Researchers at the University of Illinois looked at six decades worth\n          of death rates from U.S. hurricanes and found that hurricanes with\n          female names were more deadly because people think they sound less\n          threatening and therefore take fewer precautions to protect themselves.\n        ")]),_c('p',[_vm._v("\n          A hurricanes name is unrelated to how fierce it will be, but people are\n          subconsciously making protective decisions based on gendered storm naming.\n          "),_c('i',[_vm._v("The researchers found that the more feminine a storm name, the more\n          fatalities it caused. The report suggests that changing a severe\n          hurricanes name from Charley to Eloise could triple its death "),_c('a',{attrs:{"href":"#rate","aria-describedby":"footnote-label","id":"rate-ref"}},[_vm._v("rate.")]),_vm._v("")])]),_c('p',[_vm._v("\n          Trying to make the opposite of cute, cute is not always the best option.\n          But sometimes it can definitely bring an interesting contrast, just not\n          in cases where it becomes threating like hurricanes.\n        ")]),_c('footer',[_c('ol',[_c('li',{attrs:{"id":"rate"}},[_vm._v("A. Sifferlin (2014), Hurricanes With Female Names Kill More People, Study Finds.\n              "),_c('a',{attrs:{"href":"#rate-ref","aria-label":"Back to content"}},[_vm._v("")])])])])]),_c('aside',[_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/49.png"}}),_c('figcaption',[_vm._v("Cute gang, written on a toilet door in 0T301, Amsterdam.")])])]),_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/50.png"}}),_c('figcaption',[_vm._v("Kalashnikitty.")])])]),_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/51.png"}}),_c('figcaption',[_vm._v("Girls wearing Kalashnikitty shirts, source unkown.")])])]),_c('div',{staticClass:"row"},[_c('figure',[_c('img',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/52.png"}}),_c('figcaption',[_vm._v("Image found on Beni Bischofs instagram.")])])])])])]),_c('br'),_c('br'),_c('TitleImage',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/titles/conclusion.svg"}}),_c('br'),_c('br'),_c('div',{staticClass:"container",attrs:{"id":"cuteConclusion"}},[_c('header',[_c('h1',{staticClass:"stretch"},[_c('span',{staticClass:"light-salmon"},[_vm._v("C")]),_c('span',{staticClass:"salmon"},[_vm._v("o")]),_c('span',{staticClass:"light-pink"},[_vm._v("n")]),_c('span',{staticClass:"pink"},[_vm._v("c")]),_c('span',{staticClass:"magenta"},[_vm._v("l")]),_c('span',{staticClass:"light-purple"},[_vm._v("u")]),_c('span',{staticClass:"light-blue"},[_vm._v("s")]),_c('span',{staticClass:"blue"},[_vm._v("i")]),_c('span',{staticClass:"teal"},[_vm._v("o")]),_c('span',{staticClass:"light-green"},[_vm._v("n")])])]),_c('section',[_c('main',[_c('p',[_vm._v("\n          After writing this thesis my love for cute things didnt die. By\n          researching this topic it gave me time to dig deeper and think more\n          about what shapes the cute. I can tell you its everywhere, on the\n          streets, in the supermarket, the petting zoo, the other zoo, on\n          television, in books, on clothing, food and even more.\n        ")]),_c('p',[_vm._v("\n          By writing this thesis I only scratched the surface of all the cute\n          in the world but by doing that it did gave me some insides of how not\n          all cute things are cute, but some are made cute by using certain\n          elements.\n        ")]),_c('p',[_vm._v("\n          But I have to admit that I am most interested in the turning point of\n          cute, where cute becomes a tool to achieve something that isnt all\n          that cute. Which I explained in the final chapters. Where cute becomes\n          a tool to sell something, or might even change the way we look at\n          certain things, like presidents or storms. I love the fact that cute\n          can be surprising and will always be a subjective and sometimes abstract\n          thing.\n        ")]),_c('p',[_vm._v("\n          I saw that cuteness is often combined with naivety and childishness,\n          which sometimes is normal. But cute can also be used as a mechanism\n          to protect the evil message behind it. Many people compare cute with\n          innocent and therefore they cant see any harm in it which could be\n          dangerous as I explained in the chapters cuteness is innocent,\n          cuteness sells and cuteness and propaganda. Where cuteness is used as\n          a strategy to sell something, to convey a message that its totally\n          truthful or to change the image of a person.\n        ")]),_c('p',[_vm._v("\n          What surprised me is that I found a lot of overlaps between different\n          chapters. Also, I really liked that I was able to use some of my favorite\n          things like hip hop music and drag queens, and found out that also\n          they have a certain kind of cuteness, maybe it is more small elements\n          from hip hop artists and drag queens that are cute. But still it feels\n          like cute is everywhere. What is not so strange since cute is one of\n          our basic instincts.\n        ")]),_c('p',[_vm._v("\n          Aldo using cute, just as cute is not the most meaningful but adding\n          cute to a different subject can make it meaningful or very misleading\n          as I explained in the chapter opposites attract. Where cuteness is\n          being used as a strategy to make something that is not cute, cute by\n          adding little elements of cute to it. Which can change peoples view\n          on the subject and sometimes that can be harmful like a hurricane.\n        ")]),_c('p',[_vm._v("\n          For my graduation project I would like to find the line between cute\n          and evil and writing this thesis has brought me closer to that. I dont\n          want to say too much so I will just pose you with this final question:\n          Is Peter Pan actually a cult leader?\n        ")])]),_c('aside')])]),_c('br'),_c('br'),_c('TitleImage',{attrs:{"src":"/go-theses-19-cato-stigter/static/images/titles/bibliography.svg"}}),_c('br'),_c('br'),_c('div',{staticClass:"container",attrs:{"id":"cuteBibliography"}},[_c('header',[_c('h1',{staticClass:"stretch"},[_c('span',{staticClass:"light-salmon"},[_vm._v("b")]),_c('span',{staticClass:"salmon"},[_vm._v("i")]),_c('span',{staticClass:"light-pink"},[_vm._v("b")]),_c('span',{staticClass:"pink"},[_vm._v("l")]),_c('span',{staticClass:"magenta"},[_vm._v("i")]),_c('span',{staticClass:"light-purple"},[_vm._v("o")]),_c('span',{staticClass:"light-blue"},[_vm._v("g")]),_c('span',{staticClass:"blue"},